@font-face {
  font-family: "Istok Web";
  font-display: swap;
  src: url("../fonts/IstokWebBold.woff2") format("woff2"), url("../fonts/IstokWebBold.woff") format("woff");
  font-style: normal;
  font-weight: 700;
}
@font-face {
  font-family: "Istok Web";
  font-display: swap;
  src: url("../fonts/IstokWebRegular.woff2") format("woff2"), url("../fonts/IstokWebRegular.woff") format("woff");
  font-style: normal;
  font-weight: 400;
}
@font-face {
  font-family: "Rubik";
  font-display: swap;
  src: url("../fonts/RubikMedium.woff2") format("woff2"), url("../fonts/RubikMedium.woff") format("woff");
  font-style: normal;
  font-weight: 500;
}
@font-face {
  font-family: "Rubik";
  font-display: swap;
  src: url("../fonts/RubikRegular.woff2") format("woff2"), url("../fonts/RubikRegular.woff") format("woff");
  font-style: normal;
  font-weight: 400;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  height: 100%;
}

body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  align-content: start;
  min-height: 100%;
  margin: 0;
  margin-top: 1.1em;
  font-family: "Rubik", "Arial", sans-serif;
  font-size: 0.56rem;
  line-height: 1.2;
  color: #000000;
}

.container {
  padding: 0 1.78em;
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: top;
}

svg {
  display: block;
}

button {
  cursor: pointer;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.header {
  margin-bottom: 3.33em;
}
.header__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.toggle {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 30px;
  height: 12px;
  padding: 0;
  border: none;
  background-color: transparent;
}
.toggle span {
  width: 100%;
  height: 2px;
  background-color: #000;
}

.search {
  position: relative;
}
.search::after {
  position: absolute;
  top: 0;
  left: 0;
  width: 153px;
  height: 141px;
  background-image: url("../img/light.svg");
  content: "";
}
.search__box {
  position: relative;
  padding: 1.7em 0;
  border: 1px solid rgba(0, 0, 0, 0.5);
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}
.search__wrapper {
  display: flex;
  align-items: center;
  margin-left: 2.1em;
  margin-right: 1em;
}
.search__icon {
  margin-right: 7px;
  padding: 0;
  border: none;
  background-color: transparent;
}
.search__input {
  padding: 0;
  font-family: inherit;
  font-style: normal;
  font-weight: 400;
  font-size: 1.125rem;
  line-height: 1.3;
  color: #000000;
  border: none;
}
.search__dropdown {
  position: absolute;
  top: 54px;
  left: 0;
  width: 100%;
  background-color: white;
  border-bottom: 1px solid rgba(0, 0, 0, 0.5);
  border-radius: 10px;
}
.search__dropdown-item {
  display: flex;
  align-items: center;
  padding: 0.8em 1em 0.8em 2.1em;
}
.search__dropdown-item:first-child {
  background-color: #fef6d6;
}
.search__dropdown-item img {
  margin-right: 7px;
}
.search__dropdown-item p {
  margin: 0;
  font-weight: 500;
  font-size: 1.125rem;
  line-height: 1.3;
}

.cards {
  margin-bottom: 2.22em;
}
.cards__inner {
  display: flex;
  flex-wrap: wrap;
  gap: 20px 16px;
}

.card {
  width: 136px;
  padding: 0.5em 0.5em 0.8em 0.5em;
  border: 1px solid #000;
}
.card__img {
  margin-bottom: 0.6em;
}
.card__content {
  display: flex;
  flex-direction: column;
}
.card__content-title {
  margin-top: 0;
  margin-bottom: 1em;
  font-family: "Istok Web", "Arial", sans-serif;
  font-weight: 700;
  font-size: inherit;
  line-height: 2.4;
  color: #000000;
}
.card__content-block {
  display: flex;
  align-items: center;
  margin-bottom: 0.5em;
}
.card__content-block img {
  margin-right: 0.4em;
}
.card__content-block-name {
  margin: 0;
}
.card__content-block-status {
  display: flex;
  align-items: center;
  margin: 0;
  font-size: 0.43rem;
  line-height: 1.6;
  color: #ff9900;
}
.card__content-block-status::after {
  display: block;
  width: 10px;
  height: 9px;
  margin-left: 2px;
  background-image: url("../img/star.png");
  content: "";
}
.card__content-text {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 6px;
}
.card__content-features {
  line-height: 1.7;
}
.card__content-filter {
  padding: 0 1em;
  font-size: 0.43rem;
  line-height: 2.43;
  border: 1px solid #fef6d6;
  border-radius: 4px;
}
.card__content-price {
  padding: 0 1.29em;
  font-size: 0.43rem;
  line-height: 2.43;
  background: #fdd446;
  border-radius: 4px;
}
.card__button {
  width: 100%;
  max-width: 115px;
  padding: 4px 0;
  align-self: center;
  font-family: "Istok Web", "Arial", sans-serif;
  font-size: 0.625rem;
  line-height: 1.6;
  color: inherit;
  background: linear-gradient(90deg, #fdd446 0%, #f79b47 100%);
  border-radius: 8px;
  border: none;
}

.pagination {
  margin-bottom: 8.9em;
}
.pagination__inner {
  display: flex;
  justify-content: center;
}
.pagination__list {
  display: flex;
  margin: 0;
  padding: 0;
  list-style: none;
}
.pagination__item.active .pagination__link {
  background-color: #fdd446;
  border-radius: 50%;
}
.pagination__link {
  display: block;
  padding: 0 15px;
  font-family: "Istok Web", "Arial", sans-serif;
  font-weight: 700;
  font-size: 1.125rem;
  line-height: 2.1;
  color: #000000;
  text-decoration: none;
}

.footer {
  padding: 3.33em 0;
  background-color: #fdd446;
}
.footer__inner {
  background-image: url("../img/star-big.png");
  background-repeat: no-repeat;
  background-position: right top;
}
.footer__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.footer__item + .footer__item {
  margin-top: 5px;
}
.footer__link {
  font-size: 0.875rem;
  line-height: 1.7;
  color: inherit;
  text-decoration: none;
}